.dragEvent-wrap {
  height: 500px;
  margin-top: 20px;
  display: flex;
}

.content-wrap {
  width: 102px;
  height: 200px;
  border: solid 1px black;
  margin-left: 100px;
}

.item-text {
  background-color: blueviolet;
  width: 100px;
  text-align: center;
  margin-top: 3px;
}

.over {
  border: red solid 2px;
}
.view-wrap {
  width: 100%;
  height: calc(100vh - 50px);
}
.view-wrap .view-content-wrap {
  border: 1px solid red;
  width: 100%;
  height: 100%;
  position: relative;
}
.multiview-wrap {
  border: 1px solid black;
  position: absolute;
}
.multiview-wrap_border {
  border-color: blue;
}

.App {
  /* text-align: center; */
  position: relative;
}
